import React, { useEffect } from "react"
import { Row, Col, Icon } from "antd"
import { Link, withRouter } from "react-router-dom"
import { Section, Heading, Button, Card, Alert, GifPlayer } from "components"
import styled from "styled-components"
import ShareSection from "components/common/ShareSection"
import { baseStyles } from "styles/base"
import { country } from "App"
import { Trans } from "@lingui/macro"
import { countryMap } from "helpers/countryMapping"

const CutorsSection = styled(Section).attrs(() => ({
	paddingHorizontal: "0",
}))`
	background: #1890ff;
	border-radius: 8px;
	padding: 2em;
	margin-bottom: 4em;
	h4.ant-typography {
		margin-bottom: 0.5em;
	}
	.ant-typography,
	a {
		color: #fff;
	}
`

function RegisterSuccess({ location, history }) {
	const state = location.state || {}

	const role = state.rolePvid

	useEffect(() => {
		if (!state.success) history.push(`/${country}/register`)
	}, [history, state.success])

	return (
		<Section style={{ marginTop: "2em" }}>
			<Row type="flex" justify="center" align="middle">
				<Col lg={12}>
					<Card title="" autoHeight>
						<Section centered textAlign="center">
							<GifPlayer src="https://assets9.lottiefiles.com/temp/lf20_4Jjg2l.json" width={250} height={250} class="mb2em" />
							<Heading
								bold
								content={<Trans>Thank you! :)</Trans>}
								subheader={
									<Trans>
										Hey, thank you for registering at Cudy! Now please take a look at your inbox and verify your
										account. Don't worry, won't take you long. We promise.
									</Trans>
								}
								marginBottom="3em"
							/>
							<Alert message={<Trans>Don't forget to check your spam/junk folder too :)</Trans>} type="info" showIcon />{" "}
							<Row type="flex" justify="center" className="mb2em">
								<Col lg={18}>
									<Link to={`/${country}/account/resend_email`}>
										<Trans>Didn't receive the verification email?</Trans>
									</Link>
								</Col>
							</Row>
							<Row type="flex" justify="center" style={{ marginBottom: "2em" }}>
								<Col lg={18} style={{ textAlign: "center" }}>
									<Trans>
										<p style={{ color: baseStyles.greyColor }}>Mind to spread some words?</p>
									</Trans>
									<ShareSection isNotLoggedin />
								</Col>
							</Row>
							{role === 2 && (
								<CutorsSection>
									<Heading
										content={
											<Trans>
												Join Cutors!{" "}
												<span role="img" aria-text="Cutors">
													🙋🏼‍♂️
												</span>
											</Trans>
										}
										subheader={
											<Trans>
												If you are a tutor, don't forget to join our dedicated community of Tutors in{" "}
												{(countryMap[state.country] || {}).name} as well. We've been waiting for you.
											</Trans>
										}
									/>
									<Trans>
										<a href={(countryMap[state.country] || {}).cutorsLink} target="__blank">
											Join now <Icon type="right" />
										</a>
									</Trans>
								</CutorsSection>
							)}
						</Section>
						<Section textAlign="center" centered paddingHorizontal="normal">
							<Link to={`/${country}`}>
								<Trans>
									<Button type="ghost" icon="home">
										Back to Home
									</Button>
								</Trans>
							</Link>
						</Section>
					</Card>
				</Col>
			</Row>
		</Section>
	)
}

export default withRouter(RegisterSuccess)
